import { Box, Typography } from "@mui/material";
import { Grid } from "@mui/material";
import { Link } from "react-router-dom";

import data from "../../assets/data.json";
import { RegularText } from "../shared/common-styles";

const ContactDetails = () => {
  return (
    <Box sx={{ pt: { xs: "34px", md: "68px" }, m: "60px 80px 60px" }}>
      <Grid container spacing={"2px"} justifyContent="center">
        {data.contactLinks.map((socialLink, index) => (
          <Grid item sx={{ mb: "2rem" }} xs={12} md={6} lg={3} key={index}>
            <Link
              to={socialLink.linkUrl}
              target="_blank"
              rel="noopener noreferrer"
              style={{ textDecoration: "none" }}
            >
              <img
                className="image"
                src={require(`../../${socialLink.imageUrl}`)}
                alt={socialLink.title}
              />
              <Typography
                component="h1"
                variant="h5"
                color="#3A3A3A"
                fontWeight="700"
                sx={{ my: "0.5rem" }}
              >
                {socialLink.title}
              </Typography>
              {socialLink.name && (
                <RegularText sx={{ mx: "20px", mb: "8px" }}>
                  {socialLink.name}
                </RegularText>
              )}
              {socialLink.position && (
                <RegularText sx={{ mx: "20px", mb: "8px" }}>
                  {socialLink.position}
                </RegularText>
              )}
              {socialLink.contact && (
                <RegularText sx={{ mx: "20px", mb: "8px" }}>
                  {socialLink.contact}
                </RegularText>
              )}
            </Link>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

export default ContactDetails;
