import "./App.css";
import Home from "./Modules/home/Home";
import Header from "./components/Header";
import Footer from "./Modules/home/Footer";
import { Routes, Route, Navigate } from "react-router-dom";
import ContactUs from "./Modules/contact-us/ContactUs";
import AboutUsPage from "./Modules/about-us/AboutUsPage";
import FoodSafetyQuality from "./Modules/food-safety-quality/FoodSafetyQuality";
import NonVeg from "./Modules/products/NonVeg";
import Veg from "./Modules/products/Veg";

function App() {
  return (
    <div className="App">
      <Header />
      <Routes>
        <Route path="/home" element={<Home />} />
        <Route path="/about-us" element={<AboutUsPage />} />
        <Route path="/about-us/:tab" element={<AboutUsPage />} />
        <Route path="/food-and-safety" element={<FoodSafetyQuality />} />
        <Route path="/food-and-safety/:tab" element={<FoodSafetyQuality />} />

        <Route path="/products/veg" element={<Veg />} />
        <Route path="/products/non-veg" element={<NonVeg />} />

        <Route path="/contact-us" element={<ContactUs />} />
        <Route path="*" element={<Navigate to="/home" />} />
      </Routes>
      <Footer />
    </div>
  );
}

export default App;
