import { useState } from "react";
import { MenuItem, Menu as MuiMenu, styled } from "@mui/material";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";

const StyledLabel = styled("span")`
  display: flex;
  align-items: center;
  cursor: pointer;
`;

interface MenuProps {
  label: string;
  menuItems: {
    label: string;
    linkUrl: string;
  }[];
  onItemClick: (url: string) => void;
  isNavActive: boolean;
}

const CustomMenu: React.FC<MenuProps> = ({
  label,
  menuItems,
  onItemClick,
  isNavActive,
}) => {
  const [menu, setMenu] = useState<null | HTMLElement>(null);
  const open = Boolean(menu);

  const handleMenuClick = (event: React.MouseEvent<HTMLElement>) => {
    setMenu(event.currentTarget);
  };

  const handleMenuClose = (url: string) => {
    onItemClick(url);
    setMenu(null);
  };

  return (
    <div>
      <StyledLabel
        id="fade-button"
        aria-controls={open ? "fade-menu" : undefined}
        aria-haspopup="true"
        aria-expanded={open ? "true" : undefined}
        onClick={handleMenuClick}
        sx={{ color: isNavActive ? "#FF8D00" : "#3A3A3A" }}
      >
        {label}
        <KeyboardArrowDownIcon />
      </StyledLabel>
      <MuiMenu
        id="fade-menu"
        MenuListProps={{
          "aria-labelledby": "fade-button",
        }}
        anchorEl={menu}
        open={open}
        onClose={handleMenuClose}
      >
        {menuItems?.map((tab, index) => (
          <MenuItem key={index} onClick={() => handleMenuClose(tab.linkUrl)}>
            {tab.label}
          </MenuItem>
        ))}
      </MuiMenu>
    </div>
  );
};

export default CustomMenu;
