import * as React from "react";
import Box from "@mui/material/Box";
import ImageList from "@mui/material/ImageList";
import ImageListItem from "@mui/material/ImageListItem";
import { StyledTitle } from "../shared/common-styles";
import data from "../../assets/data.json";

const BestSellers = () => {
  return (
    <Box sx={{ pt: { md: "20px" } }}>
      <StyledTitle>Our Best Sellers</StyledTitle>
      <ImageList
        variant="masonry"
        cols={3}
        gap={8}
        sx={{ mt: { xs: "8px", md: "16px" } }}
      >
        {data.bestSellers.map((item, index) => (
          <ImageListItem key={index}>
            <img
              src={require(`../../${item.imageUrl}`)}
              srcSet={require(`../../${item.imageUrl}`)}
              alt={item.title}
              loading="lazy"
            />
          </ImageListItem>
        ))}
      </ImageList>
    </Box>
  );
};

export default BestSellers;
