import { StyledTitle } from "../shared/common-styles";

const SocialLinks = () => {
  return (
    <Box>
      <StyledTitle>Social</StyledTitle>
      <Grid
        container
        spacing={"8px"}
        justifyContent="center"
        sx={{ gap: "60px" }}
      >
        {data.socialLinks.map((socialLink, index) => (
          <Grid item sx={{ mb: "1rem" }} key={index}>
            <Link
              to={socialLink.linkUrl}
              target="_blank"
              rel="noopener noreferrer"
              style={{ textDecoration: "none" }}
            >
              <img
                className="image"
                src={require(`../../${socialLink.imageUrl}`)}
                alt={socialLink.title}
              />
              <Typography
                component="h1"
                variant="h5"
                color="#3A3A3A"
                fontWeight="300"
                fontSize="24px"
                sx={{ my: "0.5rem" }}
              >
                {socialLink.title}
              </Typography>
            </Link>
          </Grid>
        ))}
      </Grid>
    </Box>
  );
};

export default SocialLinks;
