import { Box, Tab, TabProps, Tabs } from "@mui/material";
import styled from "@emotion/styled";

import { LinkProps, Link } from "react-router-dom";
import { useEffect, useState } from "react";
import { useParams } from "react-router";
import BackgroundWithTitle from "../shared/components/BackgroundWithTitle";
import QualityAssurance from "./QualityAssurance";
import Certifications from "./Certifications";
import foodAndSafetyBg from "../../assets/food-and-safety-bg.png";

const LinkTab: React.ComponentType<TabProps & LinkProps> =
  Tab as React.ComponentType<TabProps & LinkProps>;

const StyledBox = styled(Box)`
  @media (max-width: 1439px) {
  }
`;

const StyledTabs = styled(Tabs)`
  .MuiTabs-indicator {
    display: none;
  }

  .MuiTabs-flexContainer {
    gap: 20px;
  }
`;

const StyledLinkTab = styled(LinkTab)`
  background: #f5f5f5;
  border-radius: 80px;
  height: 60px;
  color: #3a3a3a;
  font-size: 20px;
  font-weight: 300;
  min-width: 290px;

  &.Mui-selected {
    background: #ff8d00;
    color: #fff;
    font-weight: 600;
  }
`;

const FoodSafetyQuality = () => {
  const params = useParams();
  const [value, setValue] = useState<number>();

  useEffect(() => {
    setValue(params.tab === "quality-assurance" ? 1 : 0);
  }, [params]);

  const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setValue(newValue);
  };

  return (
    <StyledBox
      sx={{
        m: { xs: "30px 20px", md: "40px 0", xl: "auto" },
        maxWidth: "1440px",
        margin: "auto",
      }}
    >
      <BackgroundWithTitle
        title="Food Safety & Quality"
        bgImage={foodAndSafetyBg}
      />
      <StyledTabs value={value} onChange={handleChange} centered>
        <StyledLinkTab
          label="Certifications"
          component={Link}
          to="/food-and-safety"
        />
        <StyledLinkTab
          label="Quality Assurance"
          component={Link}
          to="/food-and-safety/quality-assurance"
        />
      </StyledTabs>
      {value === 0 ? <Certifications /> : <QualityAssurance />}
    </StyledBox>
  );
};

export default FoodSafetyQuality;
