import { Box, styled, IconButton, Drawer, List, ListItem } from "@mui/material";
import { NavHashLink } from "react-router-hash-link";
import { useLocation } from "react-router-dom";
import MenuIcon from "@mui/icons-material/Menu";
import { useNavigate } from "react-router-dom";

import data from "../assets/data.json";
import Logo from "../Modules/shared/components/Logo";
import { RegularText } from "./../Modules/shared/common-styles";
import { useState } from "react";
import CustomMenu from "../Modules/shared/components/CustomMenu";

const Container = styled(Box)`
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;

  @media (max-width: 1239px) {
    .mobile-item-list {
    }

    .web-item-list {
      display: none;
    }
  }

  @media (min-width: 1240px) {
    .web-item-list {
      display: flex;
      gap: 2rem;
    }

    .mobile-item-list {
      display: none;
    }
  }
`;

const Header = () => {
  const [isDrawerOpen, setIsDrawerOpen] = useState(false);

  const navigate = useNavigate();

  const { pathname } = useLocation();

  const isNavActive = (hash: string) => pathname.includes(hash);

  const onNavChange = (url: string, isDropdown: boolean) => {
    if (!isDropdown) {
      setTimeout(() => {
        navigate(url);
      }, 100);
      setIsDrawerOpen(false);
    }
  };

  return (
    <Container sx={{ p: { xs: "0 8px", md: "12px 60px" } }}>
      <Logo />
      <div className="web-item-list">
        {data.headerNavigationList.map((navigation, index) =>
          navigation.isDropdown ? (
            navigation.tabs && (
              <CustomMenu
                label={navigation.label}
                menuItems={navigation.tabs}
                onItemClick={(url) => onNavChange(url, false)}
                isNavActive={isNavActive(navigation.linkUrl)}
                key={index}
              />
            )
          ) : (
            <NavHashLink
              style={{
                color: isNavActive(navigation.linkUrl) ? "#FF8D00" : "#3A3A3A",
                textDecoration: "none",
              }}
              smooth
              to={navigation.linkUrl}
              key={index}
            >
              <RegularText
                sx={{
                  fontWeight: "300",
                  fontSize: "16px",
                  color: "inherit",
                  fontFamily: "Bookman Old Style !important",
                }}
              >
                {navigation.label}
              </RegularText>
            </NavHashLink>
          )
        )}
      </div>
      <div className="mobile-item-list">
        <IconButton onClick={() => setIsDrawerOpen(true)}>
          <MenuIcon />
        </IconButton>
        <>
          <Drawer
            anchor={"right"}
            open={isDrawerOpen}
            onClose={() => setIsDrawerOpen(false)}
          >
            <List sx={{ padding: "20px" }}>
              {data.headerNavigationList.map((navigation, index) => (
                <ListItem
                  key={index}
                  sx={{ py: "6px" }}
                  disablePadding
                  onClick={() =>
                    onNavChange(navigation.linkUrl, navigation.isDropdown)
                  }
                >
                  <NavHashLink
                    style={{
                      color: isNavActive(navigation.linkUrl)
                        ? "#FF8D00"
                        : "#3A3A3A",
                      textDecoration: "none",
                    }}
                    smooth
                    to={navigation.linkUrl}
                  >
                    <RegularText
                      sx={{
                        fontWeight: "300",
                        fontSize: "16px",
                        color: "inherit",
                      }}
                    >
                      {navigation.label}
                    </RegularText>
                  </NavHashLink>
                </ListItem>
              ))}
            </List>
          </Drawer>
        </>
      </div>
    </Container>
  );
};

export default Header;
