import { styled } from "@mui/material";
import { useState } from "react";

const StyledContainer = styled("div")`
  background: #f5f5f5;
  padding: 64px 80px 40px;
`;

const StyledFormControl = styled("div")`
  margin-bottom: 24px;
  gap: 36px;

  label {
    display: flex;
    flex-direction: column;
    font-size: 12px;
    font-weight: 600;
    color: black;
    align-items: flex-start;
    width: 100%;

    input,
    textarea {
      border: none;
      border-bottom: 1px solid #8d8d8d;
      background: #f5f5f5;
      margin-top: 12px;
      width: 100%;

      &:focus-visible {
        outline: none;
      }
    }
  }

  &.email-phone-number {
    display: flex;
  }
`;

interface IContactUsForm {
  fullName?: string;
  email?: number;
  phoneNumber?: number;
  selectSubject?: string;
  message?: string;
}

const ContactUsForm = () => {
  const [inputs, setInputs] = useState<IContactUsForm>();

  const handleChange = (event: any) => {
    const name = event.target.name;
    const value = event.target.value;
    setInputs((values) => ({ ...values, [name]: value }));
  };

  const handleSubmit = (event: any) => {
    event.preventDefault();
    console.log(inputs);
  };

  return (
    <StyledContainer>
      <form onSubmit={handleSubmit}>
        <StyledFormControl>
          <label>
            Full Name
            <input
              type="text"
              name="fullName"
              value={inputs?.fullName || ""}
              onChange={handleChange}
            />
          </label>
        </StyledFormControl>

        <StyledFormControl className="email-phone-number">
          <label>
            Email
            <input
              type="text"
              name="email"
              value={inputs?.email || ""}
              onChange={handleChange}
            />
          </label>
          <label>
            Phone Number
            <input
              type="number"
              name="phoneNumber"
              value={inputs?.phoneNumber || ""}
              onChange={handleChange}
            />
          </label>
        </StyledFormControl>

        <StyledFormControl>
          <label>
            Select Subject?
            <textarea
              rows={3}
              name="selectSubject"
              value={inputs?.selectSubject || ""}
              onChange={handleChange}
            />
          </label>
        </StyledFormControl>

        <StyledFormControl>
          <label>
            Message
            <textarea
              rows={3}
              name="message"
              value={inputs?.message || ""}
              onChange={handleChange}
            />
          </label>
        </StyledFormControl>
      </form>
    </StyledContainer>
  );
};

export default ContactUsForm;
