import { Button as MaterialButton, styled } from "@mui/material";

const StyledButton = styled(MaterialButton)`
  height: 52px;
  padding: 1rem;
  font-weight: 800;
  color: black;
  background: white;
  border-radius: 8px;
  padding: 16px 28px;

  &.primary {
    background: #ff8d00;
    color: white;
  }
`;

type variants = "primary" | "primary-outline" | "secondary" | "success";

interface BaseButtonProps {
  disabled?: boolean;
  children: React.ReactNode;
  type?: "button" | "submit" | "reset";
  variant: variants;
  onClick?: () => void;
}

const Button: React.FC<BaseButtonProps> = ({
  disabled,
  children,
  onClick,
  variant,
  ...rest
}) => {
  return (
    <StyledButton className={`button ${variant}`} {...rest}>
      {children}
    </StyledButton>
  );
};

export default Button;
